:root {
  --content-width: 75rem;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  text-decoration: none;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

@font-face {
  font-family: "GTAmerica-Medium";
  src: url("../../fonts/GT-America-Medium.OTF") format("opentype");
}
@font-face {
  font-family: "GTAmerica-Regular";
  src: url("../../fonts/GT-America-Regular.OTF") format("opentype");
}
@font-face {
  font-family: "GTAmerica-RegularItalic";
  src: url("../../fonts/GT-America-Regular-Italic.OTF") format("opentype");
}
html {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "GTAmerica-Medium", Helvetica, Arial, serif;
  font-weight: 100;
  line-height: 1;
}

strong, b {
  font-family: "GTAmerica-Medium", Helvetica, Arial, serif;
  font-weight: 100;
}

p {
  margin-bottom: 1.5em;
  font-size: 34px;
  color: rgb(33, 31, 31);
  letter-spacing: -0.5px;
  line-height: 44px;
  font-family: "GTAmerica-Regular", Helvetica, Arial, serif;
}
p em {
  font-style: italic;
}

img {
  max-width: 100%;
}

header {
  display: flex;
  position: absolute;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background: #fff;
  margin: 0 6em;
  padding: 30px 0;
}

.page {
  padding: 5vh 5vw 10vh;
  margin: 0 auto;
  max-width: var(--content-width);
}
.page .sticky-headline {
  position: absolute;
  display: block;
  bottom: 20px;
  left: 0;
  right: 0;
  text-align: center;
  font-family: "GTAmerica-Medium", Helvetica, Arial, serif;
  font-size: 34px;
  color: #212020;
  letter-spacing: -1px;
}

main {
  position: relative;
}
main .hero-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin-top: -5vh;
}
main .hero-wrapper img {
  width: auto;
  max-height: 60vh !important;
}
main .home-about-text {
  margin-bottom: 8rem;
  margin-top: 5rem;
}
main .home-about-text p:before {
  content: "About";
  padding-right: 3em;
  font-size: 24px;
  letter-spacing: 0;
  font-family: "GTAmerica-Medium", Helvetica, Arial, serif;
}
main .home-about-text .view-our-companies {
  margin-top: 1em;
  font-size: 24px;
  font-weight: 500;
}
main .home-about-text span {
  color: #000;
  padding: 0.6rem;
}

.blog-section {
  display: flex;
  flex-direction: row;
  padding-top: 50px;
  padding-bottom: 50px;
  gap: 2.4rem;
  border-bottom: 1px solid #dadada;
}
.blog-section .left-container {
  flex: 1;
  justify-content: space-between;
}
.blog-section .right-container {
  flex: 0.9;
}

.footer-grid {
  display: flex;
  flex-direction: row;
  padding-top: 50px;
  padding-bottom: 50px;
  gap: 1.4rem;
}

h2 {
  display: block;
  font-size: 1.5em;
  margin-bottom: 1.5rem;
}

.round-button {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 20px;
  font-size: 18px;
  line-height: 1;
  font-family: "GTAmerica-Medium", Helvetica, Arial, serif;
  margin: 5px 5px 0 0;
  cursor: pointer;
  min-width: 144px;
  text-align: center;
  transition: all 0.2s ease-in;
  border: 1px solid #dadada;
  background: #fff;
  color: #000;
  bottom: 0;
}
.round-button:hover {
  color: #fff;
  background-color: #000;
}

footer {
  padding: 5vh 12vw 1vh;
  background: rgba(33, 32, 32, 0.83);
  color: white;
  width: 100%;
  line-height: 1.5em;
  padding-bottom: 2rem;
}